.pictureContainer {
  width: 100px;
  height: 100px;
  overflow: hidden;
  text-align: center;
}
.pictureContainer img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.available-icon {
  font-size: 1.2rem;
}
.available-icon--check {
  color: green;
}
.available-icon--close {
  color: red;
}
